.home-container {
    /* scroll-snap-align: center; */
    background: url('../../resources/aurora1.jpg') repeat scroll center center/cover;
    height: 100vh;
    width: 100%;
    display: flex;
    justify-content: center;
    flex-direction: column;
    color:  whitesmoke;
}
.typed h1{
    font-size: xx-large;
    text-transform: uppercase;
    font-weight: bold;
    text-align: left;
    margin-left: 15%;
    margin-bottom: 10px;
}
.home-p {
    margin-top: 5px;
    font-size: large;
    letter-spacing: 3px;
    font-weight: bolder;
    text-align: left;
    margin-left: 15%;
    margin-right: 15%;
    line-height: 17px;
}
.home-brand {
    font-weight: bold;
    text-align: justify; 
    text-justify: inter-word;
    line-height: 24px;
    width: 40%;
    align-self: center;
    padding: 15px;
    margin: 5% auto;
    /* margin-left: 10%;
    margin-top: 5%; */
    background: rgba(76, 175, 80, 0.65)
}
@media only screen and (max-width: 600px) {
    .home-brand {
      width: 90%;
    }
  }